<template>
	<view class="page">
		<!-- 快递单号显示区域 -->
		<view class="number-list-box">
			<scroll-view scroll-y="true" class="number-list-scroll-view">
				<view class="scroll-view-item">ZTO12345671455</view>
			</scroll-view>
			<view class="scroll-view-total-number">
				{{ i18n.successfulScanningNumber }} 
				<view class="total-number">18</view>
				{{ i18n.piece }}
			</view>
		</view>
		<!-- 拣货单号列表区域 -->
		<view class="picking-list-item">
			<!-- 货架区域 -->
			<view class="shelf-box">
				<!-- 货架 -->
				<view class="goods-shelves-box">
					<view class="title-decorate"></view>
					<view>16-C-376</view>
				</view>
				<!-- 仓库 -->
				<view class="warehouse-box">
					xx库区xx仓库
				</view>
			</view>
			<!-- 包裹详情区域 -->
			<view class="package-details-box">
				<!-- 国内物流 -->
				<view class="detail-item">
					<view class="item-icon">
						<text class="iconfont icon-wuliu-"></text>
					</view>
					<view>国内物流：顺丰</view>
				</view>
				<!-- 重量 -->
				<view class="detail-item flex-end">
					<view class="item-icon">
						<text class="iconfont icon-zhongliang"></text>
					</view>
					<view>重量：0.101kg</view>
				</view>
				<!-- 所属楼层 -->
				<view class="detail-item">
					<view class="item-icon">
						<text class="iconfont icon-louceng"></text>
					</view>
					<view>所属楼层：一楼</view>
				</view>
				<!-- 体积 -->
				<view class="detail-item flex-end">
					<view class="item-icon">
						<text class="iconfont icon-gengduodaxiaoxiangsu"></text>
					</view>
					<view>体积：0.901m³</view>
				</view>
				<!-- 快递单号 -->
				<view class="detail-item width-100">
					<view class="item-icon">
						<text class="iconfont icon-dingdanhao"></text>
					</view>
					<view>快递单号：ZTO1346161</view>
				</view>
				<!-- 运输方式 -->
				<view class="detail-item">
					<view class="item-icon">
						<text class="iconfont icon-yunshu"></text>
					</view>
					<view>运输方式：空运</view>
				</view>
				<!-- 包裹异常图章 -->
				<view class="abnormal-package">
					<image src="@/static/img/abnormal-package.png" mode="widthFix"></image>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {

			}
		},
		onLoad() {
			// 中英切换顶部的 title
			uni.setNavigationBarTitle({
				title: this.$t('packageAbnormalList.navBarTitle')
			})
		},
		
		computed: {
			i18n() {
				return this.$t('packageAbnormalList')
			}
		}
	}
</script>

<style lang="scss" scoped>
.page{
	padding: 0 30rpx;
	.number-list-box{
		margin-top: 30rpx;
		padding: 30rpx 20rpx;
		height: 500rpx;
		background: #EFF5F7;
		border-radius: 10rpx;
		.number-list-scroll-view{
			height: 400rpx;
			.scroll-view-item{
				font-size: 30rpx;
				color: $color;
				margin-bottom: 20rpx;
			}
			.scroll-view-item:last-of-type{
				padding-bottom: 300rpx;
			}
		}
		.scroll-view-total-number{
			display: flex;
			align-items: center;
			font-size: 32rpx;
			color: #333333;
			justify-content: flex-end;
			.total-number{
				font-size: 34rpx;
				font-weight: bold;
				color: $color;
				margin-right: 4rpx;
			}
		}
	}
	.picking-list-item{
		margin: 40rpx 0;
		font-size: 32rpx;
		color: #333333;
		padding-bottom: 20rpx;
		border-bottom: 1rpx solid #DEDEDE;
		.shelf-box{
			@include flex-between;
			margin-bottom: 16rpx;
			.goods-shelves-box{
				display: flex;
				align-items: center;
				.title-decorate{
					@include title-decorate;
				}
			}
			.warehouse-box{
				color: $color;
			}
		}
		.package-details-box{
			@include flex-between;
			flex-wrap: wrap;
			position: relative;
			.detail-item{
				width: 50%;
				display: flex;
				align-items: center;
				margin: 12rpx 0;
				.item-icon{
					color: $color;
					width: 34rpx;
					margin-right: 16rpx;
				}
			}
			.flex-end{
				justify-content: flex-end;
			}
			.width-100{
				width: 100%;
			}
			.abnormal-package{
				position: absolute;
				bottom: 20rpx;
				right: 0rpx;
				width: 136rpx;
				height: 106rpx;
				image{
					height: 100%;
				}
			}
		}
	}
	.picking-list-item:last-of-type{
		border-bottom: none;
	}
}

</style>
